.modal_style {
    position: fixed;
    z-index: 500;
    width: 96%;
    padding: 8px;
    left: 2%;
    top: 10%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    background-color: white;
    display: inline-block;
}

@media (min-width: 576px) {
    .modal_style {
        width: 90%;
        padding: 8px;
        left: 5%;
    }
}

@media (min-width: 768px) {
    .modal_style {
        width: 60%;
        padding: 8px;
        left: 20%;
    }
}

@media (min-width: 992px) {
    .modal_style {
        width: 50%;
        padding: 16px;
        left: 24%;
    }
}

@media (min-width: 1200px) {
    .modal_style {
        width: 500px;
        padding: 16px;
        left: calc(50% - 250px);
    }
}

