.menubar {
    z-index: 200;
    position: absolute;
    width: 75%;
    left: 0;
    top: 40px;
    color: white;
    background: rgb(52,58,64);
    padding: 20px;
    font-size: 18px;
    color: rgba(255, 255, 255, 0.9);
    font-weight: 600;
    border-radius: 10px;
}

@media (min-width: 576px) {
    .menubar {
        width: 60%;
    }
}

@media (min-width: 768px) {
    .menubar {
        width: 50%;
    }
}

@media (min-width: 992px) {
    .menubar {
        width: 40%;
    }
}

@media (min-width: 1200px) {
    .menubar {
        width: 30%;
    }
}
